body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* #root {
  background-color: black;
} */

.navBar {
  width: 100%;
  height: 100px;
  background-color: rgb(2, 2, 66);
  border-bottom: 2px solid;
  border-color: yellow;
  box-shadow: 5px 3px #5c5a05;
}

.individualLink {
  color: lightblue;
  text-decoration: none;
  padding: 3px;
  margin: 0px 10px;
}

.individualLink:hover {
  color: yellow;
}

.navLinks {
  position: absolute;
  display: flex;
  left: 100px;
  right: 100px;
  top: 55px;
  justify-content: center;
}

@media screen and (max-width: 775px) {
  .navLinks {
    display: none;
  }
}

.menubar {
  position: absolute;
  top: 33px;
  right: 28px;
  height: fit-content;
  width: fit-content;
  border: 2px solid;
  border-color: rgb(2, 2, 66);
  background-color: transparent;
  padding-top: 3px;
  padding-bottom: 3px;
  border-radius: 5px;
}

.menubar:hover {
  background-color: blue;
  border-color: yellow;
}

.menulines {
  height: 3px;
  width: 25px;
  background-color: white;
}

.menulines:not(:last-child) {
  margin-bottom: 7px;
}

.planetimg {
  margin-top: 10px;
  height: 400px;
  width: auto;
  /* border: 2px solid;
  border-color: white ; */
}

.theimage {
  max-width:100%;
  max-height:100%;
  /* border: 2px solid;
  border-color: yellow; */
}

.pagetitle {
  color: yellow;
  width: 100%;
  font-size: 50px;
  padding-top: 15px;
}

p {
  color: yellow;
  margin: 0px 100px;
}

.homelink {
  position: absolute;
  top: 14px;
  left: 10px;
  height: 70px;
  width: 75px;
  border: 2px solid;
  border-color: black;
}

.homelink:hover {
  border-color: yellow;
}

.homeimage {
  max-width:100%;
  max-height:100%;
}

.sideMenu {
  position: absolute;
  width: fit-content;
  top: 100px;
  right: 0px;
  height: fit-content;
  border: 2px solid;
  border-color: yellow;
  color: yellow;
  background-image: linear-gradient(blue, black);
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.menuLink {
  padding: 15px;
}

.theLink {
  text-decoration: none;
  font-weight: bold;
  color: white;
}

.theLink:hover {
  color: yellow;
}